@mixin desktop {
    @media (max-width: 1280px) {
        @content; } }



@mixin desktop-top {
    @media (max-width: 1024px) {
        @content; } }



@mixin pad {
    @media (max-width: 767px) {
        @content; } }

@mixin pad-min {
    @media (max-width: 642px) {
        @content; } }

@mixin iphone {
    @media (max-width: 568px) {
        @content; } }

